<template>
  <div class="container">
    <div v-if="twitterAccessToken == null || !twitterAccessToken.length" class="error-container">
      Please authorize the uReflect mobile application to display your data.
    </div>

    <div v-if="!timeline.length && twitterAccessToken != null && twitterAccessToken.length" class="error-container">
      We could not retrieve any timeline... either an error occured or maybe you are not following any account yet.
    </div>

    <div v-for="tweet in timeline" :key="tweet.id" class="tweet-wrapper">
      <div class="tweet-container">

        <div class="icon-container">
          <div class="icon">
            <img :src="tweet.user.profile_image_url_https.replace('_normal', '')"/>
          </div>
          <!--<div class="loading-icon" v-if="getRequestingState">-->
          <!--<font-awesome-icon icon="spinner" class="fa-pulse" />-->
          <!--</div>-->
        </div>

        <div class="tweet">
          <div class="content-container">
            <div class="header">
              <span class="name">{{ tweet.user.name }} <font-awesome-icon v-if="tweet.user.verified"
                                                                          icon="check-circle"/></span>
              <span class="screen-name"> @{{ tweet.user.screen_name }}</span>
            </div>
            <div class="content">
              {{ tweet.full_text }}
            </div>
          </div>
          <div class="img-container">
            <!--Media ?-->
          </div>
          <div class="actions-container">
            <div class="actions-spacing" v-on:click="retweetHandler(tweet)">
                <span v-bind:class="{'retweeted' : tweet.retweeted}">
                  <font-awesome-icon icon="retweet" v-bind:class="{'retweeted' : tweet.retweeted}"/> {{ tweet.retweet_count }}
                </span>
            </div>
            <div class="actions-spacing" v-on:click="likeHandler(tweet)">
                <span v-bind:class="{'favorited' : tweet.favorited}">
                  <font-awesome-icon icon="heart" v-bind:class="{'favorited' : tweet.favorited}"/> {{ tweet.favorite_count }}
                </span>
            </div>
          </div>
        </div>

      </div>

      <hr/>
    </div>

  </div>
</template>

<script>

/* eslint-disable no-console */
/* eslint-disable no-unused-vars */
/* global CacheManager */

import axios from 'axios'
// import { CacheManager } from 'reflectos-sdk'

export default {
  name: 'Twitter',
  data: () => ({
    twitterAccessToken: '833674085196562432-noLShBs8r8NmC7OwH2dEXSnnj43pPyK',
    timeline: [],
    isLoading: true,
    isRequesting: false
    // cacheM: new CacheManager('ur-twitter')
  }),
  mounted: function () {
    // if (this.cacheM.get('accessToken')) {}
    // this.cacheM.store('nom', 'value')
    // Request access token (get it from the API)
    this.requestToken()
    this.requestTimelineTwitter('mock')
  },
  methods: {
    retweetHandler: function (tweet) {
      tweet.retweeted ? this.unretweetTweet(tweet) : this.retweetTweet(tweet)
    },
    likeHandler: function (tweet) {
      tweet.favorited ? this.unlikeTweet(tweet) : this.likeTweet(tweet)
    },
    requestToken () {
    },
    requestTimelineTwitter (param) {
      if (param === 'mock') {
        this.mockTimeline()
      } else {
        axios.get(`http://localhost:8000/timeline`, {}).then(response => {
          if (response.errors == null) {
            this.timeline = response.data
            this.isLoading = false
          }
          // catch error
        }, error => {
          console.log('ERROR UREFLECT : ' + error)
        })
      }
    },
    retweetTweet (tweet) {
      axios.post(`http://localhost:8000/retweet/${tweet.id_str}`, {}).then(response => {
        tweet.retweeted = !tweet.retweeted
        tweet.retweet_count = tweet.retweeted ? tweet.retweet_count + 1 : tweet.retweet_count - 1
      }, error => {
        console.error('ERROR UREFLECT : ' + error)
      })
    },
    unretweetTweet (tweet) {
      axios.post(`http://localhost:8000/unretweet/${tweet.id_str}`, {}).then(response => {
        tweet.retweeted = !tweet.retweeted
        tweet.retweet_count = tweet.retweeted ? tweet.retweet_count + 1 : tweet.retweet_count - 1
      }, error => {
        console.error('ERROR UREFLECT : ' + error)
      })
    },
    likeTweet (tweet) {
      axios.post(`http://localhost:8000/like/${tweet.id_str}`, {}).then(response => {
        tweet.favorited = !tweet.favorited
        tweet.favorite_count = tweet.favorited ? tweet.favorite_count + 1 : tweet.favorite_count - 1
      }, error => {
        console.error('ERROR UREFLECT : ' + error)
      })
    },
    unlikeTweet (tweet) {
      axios.post(`http://localhost:8000/unlike/${tweet.id_str}`, {}).then(response => {
        tweet.favorited = !tweet.favorited
        tweet.favorite_count = tweet.favorited ? tweet.favorite_count + 1 : tweet.favorite_count - 1
      }, error => {
        console.error('ERROR UREFLECT : ' + error)
      })
    },
    mockTimeline: function () {
      this.timeline = [
        {
          id: 1,
          user: {
            profile_image_url_https: 'https://s3-us-west-2.amazonaws.com/s.cdpn.io/1211695/me_3.jpg',
            name: 'Albert',
            verified: true,
            screen_name: 'albert_le_majordome'
          },
          full_text: 'Maitre Bruce est le meilleur',
          retweeted: false,
          favorited: false,
          retweet_count: 50,
          favorite_count: 25
        },
        {
          id: 2,
          user: {
            profile_image_url_https: 'https://s3-us-west-2.amazonaws.com/s.cdpn.io/1211695/me_3.jpg',
            name: 'Albert',
            verified: true,
            screen_name: 'albert_le_majordome'
          },
          full_text: 'Maitre Bruce est le meilleur',
          retweeted: false,
          favorited: false,
          retweet_count: 50,
          favorite_count: 25
        },
        {
          id: 3,
          user: {
            profile_image_url_https: 'https://s3-us-west-2.amazonaws.com/s.cdpn.io/1211695/me_3.jpg',
            name: 'Albert',
            verified: true,
            screen_name: 'albert_le_majordome'
          },
          full_text: 'Maitre Bruce est le meilleur',
          retweeted: false,
          favorited: false,
          retweet_count: 50,
          favorite_count: 25
        },
        {
          id: 4,
          user: {
            profile_image_url_https: 'https://s3-us-west-2.amazonaws.com/s.cdpn.io/1211695/me_3.jpg',
            name: 'Albert',
            verified: true,
            screen_name: 'albert_le_majordome'
          },
          full_text: 'Maitre Bruce est le meilleur',
          retweeted: false,
          favorited: false,
          retweet_count: 50,
          favorite_count: 25
        }
      ]
      this.isLoading = false
    }
  }
}
</script>

